<template>
	<div class="_page">
		<Header>
			<h1 slot="title">手机端代码生成器</h1>
		</Header>
		<div class="computed flex-xb">
			<div class="ev source">
				<div class="head flex-yc flex-xb">
					<p>需要计算的内容</p>

					<el-button
						size="small"
						type="primary"
						icon="el-icon-setting"
						@click="switchConfig"
					>
						{{ showConfig ? 'OK' : '设置计算规则' }}
					</el-button>
				</div>
				<textarea
					v-show="!showConfig"
					v-model="oldStyle"
				></textarea>
				<div
					class="config"
					v-show="showConfig"
				>
					<el-collapse v-model="collapseActive">
						<el-collapse-item
							title="设置"
							name="1"
						>
							<el-form
								ref="form"
								:model="config"
								label-width="10em"
							>
								<el-form-item label="是否美化代码">
									<div class="input-tip">美化代码超过{{ this.beautifyMax }}行会严重影响效率，会自动关闭美化功能，可手动开启</div>
									<el-switch
										active-color="#13ce66"
										inactive-color="#ff4949"
										v-model="config.isBeautify"
									></el-switch>
									<span style="margin-left: 20px">
										<el-tag v-if="config.isBeautify">美化</el-tag>
										<el-tag
											type="info"
											v-else
											>不美化</el-tag
										>
									</span>
								</el-form-item>
								<el-form-item label="是否自动复制结果">
									<div class="input-tip">生成代码后自动复制，大大提高效率</div>
									<el-switch
										active-color="#13ce66"
										inactive-color="#ff4949"
										v-model="config.autoCopy"
									></el-switch>
									<span style="margin-left: 20px">
										<el-tag v-if="config.autoCopy">自动</el-tag>
										<el-tag
											type="info"
											v-else
											>手动</el-tag
										>
									</span>
								</el-form-item>
								<el-form-item label="是否保留小数">
									<div class="input-tip">计算后的值是否保留小数,如果保留会保留两位</div>
									<el-switch
										active-color="#13ce66"
										inactive-color="#ff4949"
										v-model="config.decimal"
									></el-switch>
									<span style="margin-left: 20px">
										<el-tag v-if="config.decimal">保留</el-tag>
										<el-tag
											type="info"
											v-else
											>不保留</el-tag
										>
									</span>
								</el-form-item>
								<el-form-item label="记住配置">
									<div class="input-tip">记住配置，以便经常使用</div>
									<el-switch
										active-color="#13ce66"
										inactive-color="#ff4949"
										v-model="config.saveConfig"
										@change="changeSaveBtn"
									></el-switch>
									<span style="margin-left: 20px">
										<el-tag v-if="config.saveConfig">历史配置</el-tag>
										<el-tag
											type="info"
											v-else
											>默认配置</el-tag
										>
									</span>
									<span
										style="margin-left: 40px"
										v-if="history"
									>
										<el-button
											type="danger"
											round
											@click="clearHistory"
											>点击清除历史记录</el-button
										>
									</span>
								</el-form-item>
								<el-form-item label="测试信息">
									<div class="input-tip">在结果中显示匹配到的属性测试信息</div>
									<el-switch
										active-color="#13ce66"
										inactive-color="#ff4949"
										v-model="config.testExp"
									></el-switch>
									<span style="margin-left: 20px">
										<el-tag v-if="config.testExp">显示</el-tag>
										<el-tag v-else>不显示</el-tag>
									</span>
								</el-form-item>
							</el-form>
						</el-collapse-item>
						<el-collapse-item
							title="参数配置"
							name="2"
						>
							<el-table
								:data="tableData"
								height="550"
								style="width: 100%"
							>
								<el-table-column
									prop="name"
									label="名称"
									width="110"
								>
								</el-table-column>

								<el-table-column
									label="是否计算"
									width="80"
								>
									<template slot-scope="scope">
										<el-tooltip
											class="item"
											effect="dark"
											:content="scope.row.isCalc ? '计算' : '不计算'"
											placement="right"
										>
											<el-switch v-model="scope.row.isCalc"> </el-switch>
										</el-tooltip>
									</template>
								</el-table-column>

								<el-table-column
									label="计算方法"
									width="130"
								>
									<template slot-scope="scope">
										<el-input
											v-model="scope.row.multiplier"
											autocomplete="off"
										>
											<template slot="prepend">1*</template>
										</el-input>
									</template>
								</el-table-column>
								<el-table-column width="130">
									<template
										slot="header"
										slot-scope="scope"
									>
										<span>最小保护值</span>
										<el-tooltip
											class="item"
											effect="dark"
											content="小于等于这个值的文字不参与计算"
											placement="right"
										>
											<i class="el-icon-question"></i>
										</el-tooltip>
									</template>
									<template slot-scope="scope">
										<el-input
											v-model="scope.row.min"
											autocomplete="off"
										>
											<template slot="append">px</template>
										</el-input>
									</template>
								</el-table-column>

								<el-table-column width="150">
									<template
										slot="header"
										slot-scope="scope"
									>
										<span>最大保护值</span>
										<el-tooltip
											class="item"
											effect="dark"
											content="大于等于这个值的文字不参与计算"
											placement="right"
										>
											<i class="el-icon-question"></i>
										</el-tooltip>
									</template>
									<template slot-scope="scope">
										<el-input
											v-model="scope.row.max"
											autocomplete="off"
										>
											<template slot="append">px</template>
										</el-input>
									</template>
								</el-table-column>

								<el-table-column
									prop="styleEnsample"
									label="举例"
								>
								</el-table-column>
								<el-table-column
									prop="message"
									label="备注"
								>
								</el-table-column>
							</el-table>
						</el-collapse-item>
					</el-collapse>
				</div>
			</div>
			<div class="ev result">
				<div class="head flex-yc flex-xb">
					<p>计算结果:</p>

					<el-button
						size="small"
						type="primary"
						icon="el-icon-document-copy"
						@click="copyTextToClipboard($event)"
					>
						点击复制
					</el-button>
				</div>
				<pre>
            <code id="Code" class="language-css" v-html="newStyle" ref="code"></code>
          </pre>
			</div>
		</div>

		<div :class="`tips ${tipShow ? 'show' : ''}`">✅ Copy successfully !!! 🌈</div>
	</div>
</template>

<script>
import '@/assets/css/arta.min.css';
import '@/assets/css/computed-elder.css';
import Header from '@/components/Header.vue';
import hljs from '@/assets/js/highlight.min.js';
import { deepClone } from '@/assets/js/deepClone.js';
export default {
	name: 'computedMedia',
	components: { Header },
	data() {
		return {
			oldStyle: `/*
“手机端代码生成器”是将pc端代码中的px通过可自定义的计算规则生成可供手机端使用的代码

提示：
1.右上角【设置计算规则】按钮自定义计算规则
2.生成后的样式可能会影响布局
3.建议每次少段转换并核验
4.美化代码超过2000行会严重影响效率，会自动关闭美化功能，可手动开启
*/

/*示例：↓↓↓*/
.list li {
    border-bottom: 10px solid #999;
    border-bottom: 2px solid #999;
    border-radius: 10px;
    border-radius: 5px;
    border-radius: 100%;
    background: #409EFF;
    color: #fff;
}

.list {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
    margin: 30px auto 5%;
    padding: 500px 10%;
    width: 20px;
    height: 20px;

    font-size: 30px;
    font-size: 20px;
    font-size: 12px;
    transform: translateY(-50px);

    position: absolute;
    left: 10px;
    top: calc(100% - 10px);
    display: flex;
    gap: 20px 60px;
    border-width: 20px;
}`,

			collapseActive: ['1', '2'], //折叠面板

			showConfig: true, //显示配置
			showConfig: false, //显示配置
			tipShow: false, //显示提示
			newStyle: '',
			firstCloseBeautify: true,
			firstVisit: true, //第一次访问
			beautifyMax: 2000,
			history: null,
			defaultConfig: null, //用于拷贝一份初始数据
			config: {
				isBeautify: true,
				autoCopy: true,
				saveConfig: true,
				decimal: false,
				testExp: false,
			},
			tableData: [
				{
					name: '圆角',
					attr: 'border-radius',
					styleEnsample: 'border-radius: 30px;',
					isCalc: true,
					multiplier: 0.5,
					min: 10,
					max: 9999,
					message: '百分比类型不参与计算 border-radius:100%;',
				},
				{
					name: '边框',
					attr: 'border',
					styleEnsample: 'border-bottom: 10px solid #999;',
					isCalc: true,
					multiplier: 0.5,
					min: 2,
					max: 9999,
					message: '',
				},
				{
					name: 'box-shadow',
					attr: 'box-shadow',
					styleEnsample: 'box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);',
					isCalc: true,
					multiplier: 0.5,
					min: 5,
					max: 9999,
					message: '',
				},
				{
					name: '边距',
					attr: 'margin',
					styleEnsample: 'margin: 30px auto 20px;',
					isCalc: true,
					multiplier: 0.5,
					min: 0,
					max: 9999,
					message: '百分比类型不参与计算',
				},
				{
					name: '填充',
					attr: 'padding',
					styleEnsample: 'padding: 500px 20px;',
					isCalc: true,
					multiplier: 0.5,
					min: 0,
					max: 9999,
					message: '百分比类型不参与计算',
				},

				{
					name: '宽',
					attr: 'width',
					styleEnsample: 'width: 20px;',
					isCalc: true,
					multiplier: 0.5,
					min: 0,
					max: 9999,
					message: '',
				},
				{
					name: '高',
					attr: 'height',
					styleEnsample: 'height: 20px;',
					isCalc: true,
					multiplier: 0.5,
					min: 0,
					max: 9999,
					message: '',
				},
				{
					name: 'font-size',
					attr: 'font-size',
					styleEnsample: 'font-size: 30px;',
					isCalc: true,
					multiplier: 0.75,
					min: 12,
					max: 9999,
					message: '',
				},
				{
					name: 'transform',
					attr: 'transform',
					styleEnsample: 'transform: translateY(-50px);',
					isCalc: true,
					multiplier: 0.5,
					min: 0,
					max: 9999,
					message: '',
				},
				{
					name: '其他属性',
					attr: '',
					styleEnsample: 'left、top、gap...',
					isCalc: true,
					multiplier: 0.5,
					min: 0,
					max: 9999,
					message: '',
				},
			],
		};
	},
	watch: {
		oldStyle: {
			handler: function (_new, _old) {
				if (this.firstCloseBeautify) {
					var line = _new.split('\n').length;
					var max = this.beautifyMax;
					if (line > max) {
						this.$notify({
							type: 'warning',
							title: '已自动关闭代码美化',
							message: '代码超过' + max + '行，在设置项内可手动开启',
							position: 'top-right',
							duration: 1000 * 10,
							offset: 100,
						});
						this.firstCloseBeautify = false;
						this.config.isBeautify = false;
					}
				}
				this.createdNewStyle();
			},
			immediate: true,
		},
	},
	created() {
		this.defaultConfig = {
			config: deepClone(this.config),
			tableData: deepClone(this.tableData),
		};
	},
	methods: {
		switchConfig() {
			this.showConfig = !this.showConfig;
			if (this.showConfig == false) {
				this.createdNewStyle();

				// 保存配置
				var history = {
					config: this.config,
					tableData: this.tableData,
				};
				localStorage.setItem('history', JSON.stringify(history));

				this.getHistory();
			}
		},
		createdNewStyle() {
			let styleList = this.oldStyle;
			// styleList = styleList.replaceAll("}", "}@@@");
			styleList = styleList.replace(/}/g, '}@@@');
			styleList = styleList.split('@@@');

			let allStyle = this.computedStyle(styleList);

			// 自动保存
			if (this.config.autoCopy && !this.firstVisit) {
				this.copyTextToClipboard(allStyle);
			}
			// 自动美化
			if (this.config.isBeautify) {
				allStyle = hljs.highlight(allStyle, { language: 'css' }).value;
			}
			this.$nextTick(() => {
				this.newStyle = allStyle;
			});
			this.firstVisit = false; //不会自动复制示例
		},
		/**
		 * 计算px=>vw
		 * 返回计算后的样式
		 */
		computedStyle(styleList) {
			let allStyle = '';
			styleList.forEach((styleEv) => {
				styleEv = styleEv.replace(/(?<={)(.*\s*(?!}))*/g, ($1) => {
					// 拆分，获取到每个属性后进行计算
					let arr = $1.replace(/;/g, ';@@@').split('@@@');
					let str = '';
					// console.log(arr);
					arr.forEach((line, i) => {
						if (line.trim() != '') {
							for (var j = 0; j < this.tableData.length; j++) {
								var config = this.tableData[j];
								if (config.isCalc && line.indexOf(config.attr) != -1) {
									var newLine = this.computedPX(line, config.multiplier, config.min, config.max);
									newLine = newLine + (this.config.testExp ? `/*${config.name}*/` : '');
									str += newLine;
									break;
								}
							}
						}
					});
					return str;
				});
				allStyle = allStyle + styleEv;
			});

			return allStyle;
		},

		/*
		 * line 要查找的目标，例如：margin: 12px 0 9px;
		 * formula 计算的数值，例如 *1920
		 * minSize 最小值，如果小于最小值则返回原值
		 * maxSize 最大值，如果大于最大值则返回原值
		 */
		computedPX(line, formula, minSize, maxSize) {
			var decimal = this.config.decimal ? 2 : 0;

			return line.replace(/(\d+px)/g, ($1) => {
				var value = parseFloat($1);
				var res = Math.floor((value * formula * 100).toFixed(decimal)) / 100;
				if (value <= minSize) {
					res = value;
				} else if (res <= minSize) {
					res = minSize;
				} else if (value >= maxSize) {
					res = value;
				}
				res = res + 'px';
				if (this.config.testExp) {
					res = res + `【原：${value}】`;
				}
				return res;
			});
		},

		clearHistory() {
			this.$confirm('此操作将删除历史记录, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning',
			})
				.then(() => {
					localStorage.removeItem('history');
					this.history = null;
					this.$message({
						type: 'success',
						message: '删除成功!',
					});
				})
				.catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除',
					});
				});
		},
		getHistory() {
			var history = localStorage.getItem('history');
			history = history && JSON.parse(history);
			if (history && history.config) {
				this.history = history;
				if (!history.config.saveConfig) {
					this.config.saveConfig = false;
					return;
				}
				this.config = history.config;
				this.tableData = history.tableData;
			}
		},

		changeSaveBtn() {
			console.log(this.config.saveConfig);
			if (this.config.saveConfig) {
				if (this.history && this.history.config) {
					this.history.config.saveConfig = this.config.saveConfig;
					this.config = this.history.config;
					this.tableData = this.history.tableData;
				}
			} else {
				var defaultConfig = this.defaultConfig;
				if (defaultConfig && defaultConfig.config) {
					// if (!history.config.saveConfig) {
					// 	this.config.saveConfig = false;
					// 	return;
					// }
					defaultConfig.config.saveConfig = this.config.saveConfig;
					this.config = defaultConfig.config;
					this.tableData = defaultConfig.tableData;
				}
			}
		},

		copyTextToClipboard(text) {
			let that = this;
			this.tipShow = false;
			// let text = this.newStyle;
			text = text || that.$refs.code.innerText;
			if (!navigator.clipboard) {
				function fallbackCopyTextToClipboard(text) {
					// 1.创建一个可选中元素
					let textArea = document.createElement('textarea');
					textArea.value = text;
					// 2.使用定位，阻止页面滚动
					textArea.style.top = '0';
					textArea.style.left = '0';
					textArea.style.position = 'fixed';
					document.body.appendChild(textArea);
					textArea.focus();
					textArea.select();
					try {
						var successful = document.execCommand('copy');
						var msg = successful ? 'successful' : 'unsuccessful';
						// console.log('Fallback: Copying text command was ' + msg);
						that.tipShow = true;
						var iii = setTimeout(() => {
							that.tipShow = false;
							clearTimeout(iii);
						}, 1000);
					} catch (err) {
						console.error('Fallback: Oops, unable to copy', err);
					}
					// 3.移除元素
					document.body.removeChild(textArea);
				}
				fallbackCopyTextToClipboard(text);
				return;
			}
			navigator.clipboard.writeText(text).then(
				function () {
					// console.log('Async: Copying to clipboard was successful!');
					that.tipShow = true;
				},
				function (err) {
					console.error('Async: Could not copy text: ', err);
				}
			);
		},
	},
};
</script>
<style scoped>
.config {
	background: #fff;
	margin: 0;
}
</style>
